/* Imports */

/* This was installed via NPM first https://github.com/necolas/normalize.css/ */
@import "normalize.css";

/*  mobile:  425px / 26.5625em;
    tablet:  740px, / 46.250em;
    tablet (well when the design breaks):  850px, / 53.125em;
    desktop: 980px / 61.250em;
    wide:    1300px / 81.250em;
*/

@custom-media --phone-viewport (width <= 26.5625em);
@custom-media --tablet-viewport (width >= 26.5625em) and (width <= 53.125em);
@custom-media --desktop-viewport (width >= 53.125em) and (width <= 61.250em;);
@custom-media --wide-viewport (width >= 61.250em) and (width <= 81.250em;);

@lost gutter 16px;


/* Start Top 2016 TV Shows styles */

body {
	background-color: #1C1D1D;
	font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", Roboto, Oxygen, Ubuntu, Cantarell, "Fira Sans", "Droid Sans", "Helvetica Neue", Arial, sans-serif, "Apple Color Emoji", "Segoe UI Emoji", "Segoe UI Symbol";
	color: white;
	-webkit-font-smoothing: antialiased;
	-moz-osx-font-smoothing: grayscale;
}

.tv-show-intro-wrap {
 background: url("/img/tv-shows-2016-header-bg.jpg") top left no-repeat;
 background-size: cover;
 margin-bottom: 6em;

 @media (--phone-viewport) {
	 background-size: 100%;
	 margin-bottom: 1em;
 }
}

.tv-show-intro {
  lost-center: 1170px;
  text-align: left;
	padding-top: 5em;
	padding-bottom: 31em;

	@media (--phone-viewport) {
		padding-left: 1em;
		padding-right: 3em;
		padding-bottom: 4em;
	}

	@media (--tablet-viewport) {
		padding-left: 1em;
		padding-right: 1em;
		padding-bottom: 4em;
	}

	span {
		color: #27FECF;
	}

  h1 {
		lost-column: 4/12;
    font-size: 4.5rem;
    font-weight: 900;
		margin: -12px 0 0 0;
		padding: 0;

		@media (--phone-viewport) {
			lost-column: 1/1;
	    font-size: 3.75rem;
			margin: 0 0 1em 0;
	  }

		@media (--tablet-viewport) {
			lost-column: 1/1;
	    font-size: 3.75rem;
			margin: 0 0 1em 0;
	  }

  }

	.intro-summary {
		lost-column: 8/12;

		@media (--phone-viewport) {
			lost-column: 1/1;
			margin-bottom: 2em;
		}

		@media (--tablet-viewport) {
			lost-column: 1/1;
			margin-bottom: 2em;
		}

		.intro-box {
			text-align: left;
			padding: 0;
			float: left;
			margin-right: 5%;
			width: 30%;

			&:last-child {
				margin-right: 0;
			}

			@media (--phone-viewport) {
				lost-column: 1/1;
				margin-bottom: 2em;
			}

			@media (--tablet-viewport) {
				lost-column: 1/1;
				margin-bottom: 2em;
			}

			h2 {
				font-size: 1.5rem;
				font-weight: 900;
				margin: 0;
				padding: 0;
			}

			ul {
				margin-bottom: 0;
			}

			li {
				line-height: 1.6;
				margin: 0 0 0 -1.4em;
				padding: 0;
				font-family: 'Merriweather', "Times New Roman", serif;

				@media (--phone-viewport) {
					margin: 0 0 0 -1em;
				}
			}
		}
	}
}

/* SVG backgrounds */
svg {
	width: 100%;
	height: inherit;

	text {
		text-anchor: middle;
	}

	#alpha {
		fill: #fff;
	}

	#title {
		letter-spacing: -35px;
		font-size: 50rem;
		font-weight: 900;

		@media (--phone-viewport) {
			font-size: 18rem;
			letter-spacing: -15px;
		}

		@media (--tablet-viewport) {
			font-size: 36rem;
			letter-spacing: -15px;
		}
	}

	#base {
		fill: #1C1D1D;
		mask: url(#mask);
	}

	#base-two {
		fill: #1C1D1D;
		mask: url(#mask-two);
	}

	#base-three {
		fill: #1C1D1D;
		mask: url(#mask-three);
	}

	#base-four {
		fill: #1C1D1D;
		mask: url(#mask-four);
	}

	#base-five {
		fill: #1C1D1D;
		mask: url(#mask-five);
	}
}



.tv-five-wrap {
	position: relative;
	width: 100%;
	height: 100%;
	margin: 0;
	padding: 0;

	.text {
	  position: relative;
	  top: 0;
	  left: 0;
	  width: 100%;
	  height: 784px;
	  z-index: 10;

		@media (--phone-viewport) {
			height: 900px;
		}
	}

	section {
	  background: url('/img/tv-show-2016-five.jpg') no-repeat -100px 0;
	  position: absolute;
		top: 0;
		left: 0;
	  width: 100%;
	  min-height: 100%;
		background-size: cover;

		@media (--phone-viewport) {
			background: url('/img/tv-show-2016-five.jpg') no-repeat -180px -50px;
		}
	}

	h3 {
		display: none;
	}

	h4 {
		color: #FED131;
		font-size: 4.5rem;
		font-weight: 900;
		margin: 0;
		padding: 0;
	}

	h5 {
		color: white;
		font-size: 2.25rem;
		font-weight: 900;
		margin: 0;
		padding: 0;
	}

	p {
		font-family: 'Merriweather', "Times New Roman", serif;
		font-size: 1.25rem;
		line-height: 1.6;
		padding-right: 2em;

		@media (--phone-viewport) {
			padding: 0;
		}

		@media (--tablet-viewport) {
			padding: 0;
		}
	}

	.tv-five-blurb {
		width: 45%;
		position: absolute;
		top: 11em;
		right: 0;
		border-top: 1px solid #FED131;
		border-left: 1px solid #FED131;
		border-bottom: 1px solid #FED131;
		padding: 2em 0 2em 2em;
		z-index: 11;

		@media (--phone-viewport) {
			width: 90%;
			top: 10em;
			left: 0.5%;
			padding: 1em;
			border: 1px solid #FED131;
		}

		@media (--tablet-viewport) {
			width: 90%;
			top: 10em;
			left: 2.5%;
			padding: 1em;
			border: 1px solid #FED131;
		}
	}

	a {
		text-decoration: none;
		border-bottom: 1px solid rgba(254, 209, 49, 0.5);
		transition: all 0.2s ease-in-out;
		color: #FED131;

		&:hover {
			border-bottom: 1px solid #FED131;
		}
	}
}

